import { Button } from "@chakra-ui/react";
import { useState } from "react";
import { useNavigate } from "react-router";

function Tracking() {
  const [tracking, setTracking] = useState("");
  const tg = window.Telegram.WebApp;
  const backButton = tg.BackButton;
  const navigate = useNavigate();
  backButton.show();
  backButton.onClick(() => {
    backButton.hide();
    navigate("/");
  });

    const getTrack = (eventObj) => {
    eventObj.preventDefault();
    console.log();
    const response =  fetch(
      `https://api.track24.ru/tracking.json.php?apiKey=d7bd08383f431cf8913f1eb29585c01a&domain=localhost&pretty=true&code=}`
    );
    const track =  response.json();

    if (track.status == "ok") {
      setTracking(
        <div className="track_info">
          <p className="track_info_header">  {}</p>
          <p className="track_info_addrstate"> :</p>
          <p className="track_info_state">
            {track.data.destinationPostalAddress}
          </p>
          <p className="track_info_addrstate"> :</p>
          <p className="track_info_state">
            $
            {
              track.data.events[track.data.events.length - 1]
                .operationAttributeTranslated
            }
          </p>
        </div>
      );
    } else {
      setTracking("");
    }
  }
  return (
    <div id="main_tracking">
      <div class="dostavka_content">
        <div className="main_tracking">
          <h1
            id="dostavka_header"
            style={{ fontSize: "24px", textAlign: "center" }}
          >
            
          </h1>
          <p id="advance_help">
             - ,    .
          </p>
          <form>
            <input
              placeholder=" "
              className="gray_input"
              type="text"
              name="track_input"
              id="track_input"
            />
            <button type="submit"></button>
          </form>
        </div>
        {tracking}
      </div>
    </div>
  );
}
export default Tracking;
